.home {
    border: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-image: url(/static/media/moving-box.c475c14b.jpg);
    background-size: cover;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    border-radius: 0;
    overflow: hidden;
}

.home h1 {
    font-family: 'Comfortaa', cursive;
    font-size: 50px;
    color: white;
    /* margin-top: 3em; */
}

.home h1 span {
    color: #1aa5ff;
}

.home section {
    border: 0;
    height: 30vh;
    width: 50%;
    display: flex;
    justify-content: space-around;
    max-width: 500px;
    flex-direction: column;
    align-items: center;
    padding: 5%;
}
#component-button-next {
    background-color: #4cd1a0;
    color: #fff;
    font-size: 20px;
    justify-content: center;
    border-radius: 8px;
    margin-top: 10px;
    border: none;
}

#component-button-next:hover {
    background-color: #1aa5ff;
}
.input {
    font-size: 15px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-start;
}

.input input {
    width: 100%;
}

.input label {
    color: #3a3939;
}
  
/* .input input:focus {
outline: none;
border-color:#048e99;
box-shadow: 0 0 10px #048e99;
} */
.complement-property {
    padding-top: 20px;
}

.apt {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#aptNumber {
    width: 80%;
}

#stgNumber {
    width: 80%;
}

.has-lift {
    width: auto;
    color: white;
    font-size: 13px;
}

.has-lift label {
    background-color: #1aa5ff;
    margin-right: 10px;
    border-radius: 15px;
    width: auto;
    padding: 0.5em 1em;
}

.has-lift input {
    display: none;
}

.has-lift legend {
    border: none;
    font-size: 15px;
    font-weight: bold;
    color:#3a3939;
    margin-bottom: 5px;
}

input:checked + label{
    background-color: darkblue;
}
.comments {
    padding-top: 20px;
}
.portage-date {
    background-color: #e9e9e9;;
    height: auto;
    padding: 5%;
    padding-bottom: 2em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 15px;
}

.portage-date h2 {
    margin-bottom: 1em;
    font-weight: bold;
}

.portage-date fieldset {
    margin-bottom: 2em;
}

.hourPeriod {
    width: auto;
    color: white;
    font-size: 13px;
}

.hourPeriod label {
    background-color: #1aa5ff;
    margin-right: 10px;
    border-radius: 15px;
    width: auto;
    padding: 0.5em 1em;
}

.hourPeriod input {
    display: none;
}

.hourPeriod legend {
    border: none;
    font-size: 15px;
    font-weight: bold;
    color:#3a3939;
    margin-bottom: 5px;
}

input:checked + label{
    background-color: darkblue;
}

.otherHourPeriod {
    padding: 20px 0 0 0;
}

.pedido {
    margin: 40px auto 30px;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.pedido h1 {
    font-weight: bold;
    color: white;
    text-align: center;
    margin-bottom: 10px;
}

.pedido fieldset {
    margin: 10px 0 10px 0;
    padding: auto;    
    /* box-shadow: 5px 5px 5px rgba(0,0,0,0.5); */
}

.origem {
    background-color: #e9e9e9;
    height: auto;
    padding: 5%;
    padding-bottom: 5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.origem h2 {
    margin-bottom: 1em;
    font-weight: bold;
    color: #3a3939;
}

.origem fieldset {
    margin-bottom: 2em;
}

.property-options {
    width: auto;
    color: white;
    height: auto;
    font-size: 13px;
}


.property-options label {
    background-color: #1aa5ff;
    margin-right: 10px;
    border-radius: 15px;
    width: auto;
    padding: 0.5em 1em;
}


.property-options input {
    display: none;
}

.property-options legend {
    border: none;
    font-size: 15px;
    font-weight: bold;
    color:#3a3939;
    margin-bottom: 5px;
}

input:checked + label{
    background-color: darkblue;
}


